@import '../common/styles/sass/variables';
.nav {
    border: 1px solid;
    width: 300px;
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    color: $primaryColor;
    padding: 3px;
    & a {
        text-decoration: none;
        color: $textColor;
        letter-spacing: 1.5px;
        /*    pointer-events: none;
            cursor: default;*/
    }
}
